




























import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import { formatDate } from '@/until/index'

@Component({
  components: {
  },
  filters: {
     getFormatDate (date: any) {
      return new Date(date).getFullYear() + '年' + (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '号'
    },
    getRepairType (type: any) {
      if (type === 'type1') {
        return '开锁服务'
      } else if (type === 'type2') {
        return '公共报修'
      } else if (type === 'type3') {
        return '个人报修'
      } else {
        return '其他报修'
      }
    }
  }
})
export default class Home extends Vue {
  $createDialog : any
  $createToast : any
  // 声明周期钩子
  model = { details: '', type: 'type1' }

  houseId = ''
  // tab切换配置
  selectedLabelDefault = 'unprocessed'
  tabBarList = [{
    label: '未处理',
    value: 'unprocessed'
  }, {
    label: '已处理',
    value: 'processed'
  }]

  // 
  page = 1
  isShowMore = true
  repairList = []

  activated () {
    // this.getWeiXinConfig()
    this.$store.commit('SET_SESSION_ID', 'noSession')
    this.pageInit()
  }

  pageInit () {
    let query = this.$route.query
    if (query.openid) {
      this.openid = query.openid
      this.getCommodityOrderList()
    } else if (query.code) {
      this.getUserInfo()
    }
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  validateHandler (result : any) {
    this.validity = result.validity
    this.valid = result.valid
    console.log(result)
    console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex)
  }
  getUserInfo () {
    let query = this.$route.query
    if (query.code) {
      const toast = this['$createToast']({
        time: 0,
        txt: '数据获取中...'
      })
      toast.show()
      this.$store.dispatch('getCommodityUserInfo', { code: query.code }).then((response) => {
        if (response.success) {
          if (response.data.openid) {
            this.openid = response.data.openid
            this.getCommodityOrderList()
            if (response.data.userInfo) {
              this.userInfo = response.data.userInfo
            }
            // this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } })
          }
        }
        toast.hide()
      })
    } else {
      console.log('不是微信登陆')
    }
  }

  tabBarChange (value:any) {
    this.page = 1
    this.repairList = []
    this.isShowMore = true
    this.getCommodityOrderList()
  }

  get houseOptions () {
    this.$store.state.houseList.forEach((obj) =>{
      obj.value = obj.id
      obj.text = obj.account_info[0].name + ' ' + obj.build_info[0].name + ' '+ obj.unitNum + '单元' + obj.floorNum + '层' + obj.roomNum + '室'
    })
    return this.$store.state.houseList
  }
  get userInfo () {
    return this.$store.state.userInfo
  }

  getCommodityOrderList () {
    const toast = this['$createToast']({
      time: 0,
      txt: '数据获取中...'
    })
    toast.show()
    this.$store.dispatch('getCommodityOrderList', { page: this.page, type: this.selectedLabelDefault }).then((response) => {
      if (response.success) {
          if (response.data.list.length > 0) {
            this.repairList = this.repairList.concat(response.data.list)
          } else {
            this.isShowMore = false
          }
      }
      toast.hide()
    })
  }

  showMoreClick () {
    this.page++
    this.getRepairList()
  }

  updateRepairState (id: String) {
    this.$store.dispatch('updateCommodityOrderState', { id: id }).then((response) => {
      if (response.success) {
        this['$createDialog']({
          type: 'alert',
          content: '处理完成',
          icon: 'cubeic-ok'
        }).show()
        this.tabBarChange()
      }
    })
  }
}
